body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 30px;
}

.container::before,
.container::after {
    content: '';
    display: table;
}

.container::after {
    clear: both;
}

.row::before,
.row::after {
    content: '';
    display: table;
}

.row::after {
    clear: both;
}

.col {
  float: left;
  width: 50%;
  box-sizing: border-box;
}

a {
  color: #61dafb;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

header {
    background-color: #39cbcc;
    border-bottom: 1px solid #39cbcc;
    color: #fff;
    padding: 30px 0; 
}

header .avatar-wrap {
    width: 20%;
    padding-right: 30px;
    border-right: 1px solid #fff;
    text-align: center;
}

header .intro {
    width: 80%;
    padding-left: 50px;
}

header .contact {
    margin-top: 15px;
}

header .contact ul {
    padding: 0;
    list-style: none;
}

header .contact ul li {
    margin-bottom: 10px;
}

header .contact span {
    font-weight: 700;
}

header .title {
    display: block;
    font-weight: bold;
    font-size: 24px;
    margin-bottom: 10px;
}

header p {
    margin: 0;
    line-height: 1.5;
}

.leek-avatar {
    border-radius: 50%;
    max-width: 150px;
    margin-bottom: 15px;
}

header h1 {
    color: #fff;
    text-transform: uppercase;
    margin: 0;
}
.cv-content h3 {
    margin-bottom: 50px;
}
.cv-content h3 span {
    padding: 10px 20px;
    background-color: #39cbcc;
    color: #fff;
    position: relative;
    display: inline-block;
    min-width: 200px;
}

.cv-content h3 span::before {
    content: '';
    display: block;
    position: absolute;
    bottom: -10px;
    left: 15px;
    width: 0px;
    height: 0px;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-top: 10px solid #39cbcc;
}

.cv-content table th {
    min-width: 100px;
    padding: 10px;
    color: #39cbcc;
}

.cv-content table td span {
    font-weight: bold;
}

.cv-content table td p {
    line-height: 1.5;
}

.content-top {
    padding: 50px 0;
}

.content-bottom {
    padding: 30px 0;
    background-color: #39cbcc;
}

.cv-content .content-bottom h3 span {
    background-color: #fff;
    color: #39cbcc;
}

.cv-content .content-bottom h3 span::before {
    border-top-color: #fff;
}

.skill-wrap ul {
    padding: 0;
    list-style: none;
}

.skill-wrap ul h4 {
    color: #fff;
}

.skill-wrap .progress {
    position: relative;
    padding: 6px;
    border-radius: 3px;
    border: 2px solid #fff;
    
}

.skill-wrap .progress-bar {
    background-color: #fff;
    display: block;
    height: 100%;
    padding-left: 20px;
    color: #363636;

}

.hobby-wrap {
    padding-left: 50px;
}

.hobby-wrap ul {
    padding-left: 15px;
    color: #fff;
}

.hobby-wrap ul li {
    margin-bottom: 10px;
}
footer {
    background-color: #20232a;
    color: #ffffff;
    padding: 15px 0;
}

footer ul {
    padding: 0;
}

footer ul li {
    display: inline-block;
    margin-right: 15px;
}

footer ul li:before {
    content: '';
    display: inline-block;
    margin-top: 7px;
    margin-right: 10px;
    float: left;
    width: 5px;
    height: 5px;
    background-color: #fff;
}

footer ul li a {
    text-decoration: none;
}

footer ul li span {
    color: #61dafb;
}
